        p De la misma manera que se redacta la misin, la visin debe hacerse con trminos sencillos y de fcil comprensin, adems, debe ser realista, precisa y enfocada en el contexto real donde desempea sus actividades. Tambin, se hace necesario escribir el espacio del tiempo en el que se desea lograr lo proyectado, aqu es importante que la direccin disee y trabaje en las estrategias con miras a cumplir con ello. Y en la redaccin se debe hacer lectura de algn grado de dificultad y de lo retador de esta, sino cuenta con estas caractersticas probablemente ser entendida como una simple actividad operativa natural de la organizacin.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Finalmente, la visin debe ser medible, pues los trminos en los que se redacte deben permitir hacer una evaluacin al cumplir el tiempo establecido, evaluar los logros obtenidos y el crecimiento de la organizacin.

    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Ejemplo de visin de una empresa ganadera

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        .cajon.color-primario.p-4.mb-4(style="background-color:#DBF9BD")
          .row.justify-content-center.align-items-center
            .col-lg-1
              figure
                img(src='@/assets/curso/temas/tema2/img22.svg', alt='Clientes, Producto o servicio, Mercado, Tecnologa, Filosofa organizacional, Calidad')
            .col-lg-11
              p Para el ao 2026 la empresa Punta de Anca quiere consolidar los procesos de comercializacin y expandirse a otros mercados a nivel regional, implementando nuevas tecnologas en los procesos de produccin de carne y leche contando con el talento humano capacitado frecuentemente y garantizando la calidad en los procesos.


    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Valores corporativos
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-8
        p Los valores son un conjunto de principios, virtudes y cualidades que rodean a una persona u objeto, y los valores corporativos son los elementos propios de cada organizacin y se constituyen en los pilares ticos y morales de estas al tiempo que se convierten en la esencia de su cultura organizacional. Estos valores corporativos son bsicamente el sello de la marca que representan y se traducen en la imagen que desean tener las organizaciones frente a sus grupos de inters:
        ul.lista-ul
          li
            i.fas.fa-check 
            | Clientes.
          li
            i.fas.fa-check 
            | Proveedores
          li
            i.fas.fa-check 
            | Colaboradores
          li
            i.fas.fa-check 
            | Accionistas.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img23.svg', alt='Clientes, Producto o servicio, Mercado, Tecnologa, Filosofa organizacional, Calidad')

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Al hacer referencia a los valores corporativos se habla de las costumbres, actitudes, conceptos, acciones, comportamientos y pensamientos que la organizacin asume como propios y adopta como normas y principios para lograr una ventaja competitiva y, de esta manera, lograr una caracterstica distinta en el posicionamiento en el mercado.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-2
        figure
          img(src='@/assets/curso/temas/tema2/img24.svg', alt='La visin')
      .col-lg-10
        p Cada organizacin tiene sus elementos propios para el desarrollo, crecimiento y posicionamiento organizacional y de ellos se debe desprender la declaracin de los valores teniendo en cuenta los elementos que influyen para definirlos: tamao, imagen, cultura, estructura, ubicacin geogrfica y todos los otros que la direccin considere importante.

    .cajon.color-secundario.p-4.mb-4(data-aos="fade-left")
      p Los valores corporativos deben estar alineados con las estrategias que disee la organizacin, esto garantiza que los grupos de inters reconozcan la esencia de la cultura organizacional de la empresa, de esta manera, cada organizacin decide hacia dnde dirigir la creacin de sus valores. Algunas se enfocan en la tecnologa como valor corporativo para ser ms competitivas, otras en la calidad de sus productos y calidez de los colaboradores y as existen diferentes estrategias para generar los valores corporativos.

    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Elementos de los valores corporativos

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Para la elaboracin de los elementos corporativos se deben tener en cuenta tres elementos:
    
    SlyderE(indicadores="izquierda").mb-4(data-aos="fade-left")
      .row
        .col-md-6.col-sm-12.mb-4.mb-md-0
          h3 El deseo o la voluntad
          p(style="max-width: 93%") El deseo que tiene la organizacin por generar un reconocimiento por parte de sus grupos de inters, esto depende de las aspiraciones e inclinaciones que se tengan como empresa. 
        .col-md-6.col-sm-12
          figure(style="max-width: 93%")
            img(src='@/assets/curso/temas/tema2/img25.png', alt='El deseo o la voluntad')
      .row
        .col-md-6.col-sm-12.mb-4.mb-md-0
          h3 La estrategia
          p(style="max-width: 93%") De qu manera se visualiza compitiendo en el mercado, como quiere ser reconocido por la competencia y clientes. 
        .col-md-6.col-sm-12
          figure(style="max-width: 93%")
            img(src='@/assets/curso/temas/tema2/img26.png', alt='La estrategia')
      .row
        .col-md-6.col-sm-12.mb-4.mb-md-0
          h3 El compromiso
          p(style="max-width: 93%") No se trata simplemente de generar un listado de los valores de la organizacin, sino que la empresa debe velar por construirlos, apropiarlos y aplicarlos en el da a da para que se conviertan filosofa y cultura organizacional. De esta manera, logran ser percibidos, reconocidos y valorados por los grupos de inters.
        .col-md-6.col-sm-12
          figure(style="max-width: 93%")
            img(src='@/assets/curso/temas/tema2/img27.png', alt='La estrategia')


    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Tipos de valores corporativos
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Los valores corporativos pueden clasificarse segn las intenciones que tenga cada empresa para formularlos y aplicarlos, como se muestra a continuacin:
    
    .bg_img_01
      .row.justify-content-center.mb-5
        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img28.svg", alt="alt").img20.m-auto(style="width=50%;")
            h4.text-center De la empresa
            p.text-center.mb-0 Estos valores son los que hacen referencia a la empresa como institucin, se plantean a partir de los deseos y voluntad de los accionistas. En este tipo se pueden listar valores como la transparencia, solidez, liderazgo.
        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img29.svg", alt="alt").img90.m-auto(style="width=50%;")
            h4.text-center De sus empleados
            p.text-center.mb-0 Estos valores se enfocan en comportamientos y conductas de comportamiento de los colaboradores, se convierten en normas de la cultura organizacional. Los valores establecidos pueden ser la lealtad, honestidad, trabajo en equipo, responsabilidad.
        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img30.svg", alt="alt").img90.m-auto
            h4.text-center Del producto o servicio
            p.text-center.mb-0 Estos valores se refieren a las cualidades que puede tener la prestacin de servicio o el desarrollo de un producto, se enfocan en valores como la calidad, oportunidad, tecnologa, cumplimiento, procesos, certificaciones.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Para profundizar en el paso a paso de la elaboracin de la misin, visin y objetivos, se invita a revisar el siguiente video:

    .row.justify-content-center.align-items-center.md-5.mb-5(data-aos="fade-left")
      .col-lg-12(data-aos="fade-left")             
        figure
          .video          
            iframe(width="560" height="315" src="https://www.youtube.com/embed/E8SNoTG2rlQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Diagnstico estratgico 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img31.svg', alt='La estrategia')
      .col-lg-8
        p Este analiza el contexto donde se desarrolla la actividad econmica de la empresa, de esta manera se convierte en el marco de referencia para el diseo de estrategias, dado que se debe realizar un anlisis interno y externo que permita conocer todos los factores que influyen positiva y negativamente en la organizacin.
        p Al inicio del diagnstico se responden preguntas especficas como:
        ul.lista-ul
          li
            i.fas.fa-check
            | Dnde se estaba?
          li
            i.fas.fa-check
            | Dnde se est?
          li
            i.fas.fa-check
            | Hacia dnde se va?

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Y a partir de estas respuestas se debe capturar y trabajar la informacin para identificar oportunidades, amenazas, debilidades y fortalezas y formular estrategias dentro de la planeacin estratgica.
        p Realizar el diagnstico estratgico no es actividad compleja, por ello, no es necesario contratar a alguien externo a la organizacin especializado en el tema para realizarlo, esto hace que la tarea puede ser desarrollada por un grupo de colaboradores de la organizacin liderados por la direccin, de esta manera, los resultados sern los ms prximos a lo esperado, pues la informacin es obtenida de manera directa y se ajusta a la realidad de la organizacin.
        p Como resultado del diagnstico, que incluye el anlisis interno y externo del entorno donde se encuentra la organizacin, se construye la matriz FODA la que da paso al planteamiento de las estrategias que permitirn el posicionamiento de la organizacin.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img32.svg', alt='La estrategia')
      .col-lg-8
        h3 Anlisis interno
        p Se debe comprender que el anlisis interno dentro de la planeacin estratgica responde a aquellas variables que se pueden controlar desde la organizacin que, aunque no todas dependen de esta, s pueden ser manejadas con un alto grado de autonoma. Las variables de este anlisis se establecen identificando los puntos positivos y negativos que tiene la organizacin para determinar los recursos a utilizar con el fin de mejorar la imagen de la empresa frente a su competencia y clientes.
        p El anlisis interno se inicia desde una evaluacin de desempeo, pero tiene como propsito una evaluacin del potencial, es decir, se evala la forma cmo se desarrollan las actividades, pero acompaada al tiempo de una reflexin hacia la capacidad de crecimiento que puede tener la organizacin y las ventajas y desventajas para satisfacer las necesidades de los clientes. Las variables que se analizan en el anlisis interno son:

    .tarjeta--container.row.mb-5(data-aos="fade-left")
      .col-md.tarjeta.color-secundario.p-5(style="background-color:#DBF9BD")
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema2/img33.svg', alt='Texto que describa la imagen')
          
        h2.text-center Fortalezas
        p Son aquellas variables en los que la organizacin es fuerte y generan una ventaja competitiva frente a la competencia en el mercado.  
        ul.lista-ul
          li
            i.lista-ul__vineta
            | Qu hacemos demasiado bien?
          li
            i.lista-ul__vineta
            | Cules son nuestras ventajas?
          li
            i.lista-ul__vineta
            | Qu activos y recursos tenemos?
          li
            i.lista-ul__vineta
            | Cul es el talento que tenemos?

      .col-md.tarjeta.color-secundario.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema2/img34.svg', alt='Debilidades')
        h2.text-center Debilidades
        p Son aquellas variables que limitan el desarrollo de las actividades y no permite responder a las necesidades de los clientes.
        ul.lista-ul
          li
            i.lista-ul__vineta
            | Qu estamos haciendo que se pudiera hacer mejor?
          li
            i.lista-ul__vineta
            | Cules son las crticas y quejas que recibimos?
          li
            i.lista-ul__vineta
            | En dnde somos vulnerables?

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Al realizar el anlisis interno se identifican variables como recursos financieros, talento humano, procesos de produccin, servicio al cliente, tiempo de respuesta, etc., y cada una de estas puede ser una fortaleza o una debilidad, todo depende de la dinmica de la organizacin.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img35.svg', alt='La estrategia')
      .col-lg-8
        h3 Anlisis externo 
        p Este anlisis, al contrario del interno, evala las variables que no pueden ser controladas por la organizacin, pero que afectan de manera directa el funcionamiento y desarrollo de las actividades de la misma. Este anlisis invita a establecer el impacto que puede tener cada una de estas variables en caso de sufrir alguna modificacin y encontrar cmo dar una respuesta rpida para lograr aprovechar las oportunidades o minimizar las amenazas. Estas variables se convierten en un factor retador para la direccin que se debe enfocar de manera correcta para disear las estrategias adecuadas.
        p El anlisis externo debe ser ms cuidadoso y aqu se deben tener en cuenta las variables que son caractersticas en la dinmica del entorno de la organizacin; para ello, se sugiere hacer un anlisis de diferentes factores, comportamientos, tendencias y eventos de naturaleza legal, econmica, ambiental, tecnolgica y geogrfica:

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-md-6.col-xl.mb-4.mb-xl-0
        .tarjeta-numerada.color-primario.p-5
          .tarjeta-numerada__numero
            .h2 1
          h3.text-center Factor geogrfico
          p.text-center Se debe hacer un anlisis de los recursos disponibles en el pas o regin como, por ejemplo, recursos naturales, acceso a servicios, calidad de vas, macrolocalizacin, medios de transporte.
          br
          br
          br


      .col-md-6.col-xl.mb-4.mb-xl-0
        .tarjeta-numerada.color-primario.p-5
          .tarjeta-numerada__numero
            .h2 2
          h3.text-center Factor legal y poltico
          p.text-center Se debe revisar la legislacin vigente, normas aplicables, relaciones con los pases vecinos, elecciones, desplazamiento, reformas, resoluciones y todos los requisitos necesarios para el funcionamiento de la empresa.          

      .col-md-6.col-xl.mb-4.mb-xl-0
        .tarjeta-numerada.color-primario.p-5
          .tarjeta-numerada__numero
            .h2 3
          h3.text-center Factor econmico
          p.text-center Se debe analizar los indicadores ms importantes y relevantes del pas como la inflacin, el PIB (Producto Interno Bruto) PIB per cpita (por habitante), devaluacin, empleo y desempleo, devaluacin.
          br
          br
      
      .col-md-6.col-xl.mb-4.mb-xl-0
        .tarjeta-numerada.color-primario.p-5
          .tarjeta-numerada__numero
            .h2 4
          h3.text-center Factor tecnolgico
          p.text-center Se debe estudiar y revisar el acceso a tecnologa, medios de comunicacin, avances tecnolgicos, conectividad en las diferentes regiones y posibilidades de innovacin.
          br
          br
          br
          br
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Estos factores del anlisis externo se resumen en dos variables del diagnstico estratgico que son las oportunidades y amenazas:

    .tarjeta--container.row.mb-5(data-aos="fade-left")
      .col-md.tarjeta.color-secundario.p-5(style="background-color:#DBF9BD")
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema2/img36.svg', alt='Oportunidades')
          
        h2.text-center Oportunidades
        p Son aquellas variables externas que pueden generar un impacto positivo en la organizacin y ser aprovechadas de la mejor manera para sacar una ventaja competitiva frente a la competencia.
        ul.lista-ul
          li
            i.lista-ul__vineta
            | Qu oportunidad existe en el mercado que pueda aprovechar?
          li
            i.lista-ul__vineta
            | Cmo se comporta mi competencia para aprovechar los vacos del mercado?
          li
            i.lista-ul__vineta
            | Qu esperan los clientes de la competencia?

      .col-md.tarjeta.color-secundario.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema2/img37.svg', alt='Amenzas')
        h2.text-center Amenazas
        p Son aquellas variables que limitan el desarrollo de las actividades y no permite responder a las necesidades de los clientes.
        ul.lista-ul
          li
            i.lista-ul__vineta
            | Qu elementos externos existen que bloquean nuestro progreso?
          li
            i.lista-ul__vineta
            | Cules condiciones econmicas estn afectando nuestro negocio?
          li
            i.lista-ul__vineta
            | Qu cambios en el mercado podran impactar nuestra empresa?

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-12
        p Para complementar el tema proceso de planeacin estratgica, con la misin y visin, se invita a ver el siguiente video:
    
    .row.justify-content-center.align-items-center.md-5.mb-5
      .col-lg-12(data-aos="fade-left")     
        figure
          .video          
            iframe(width="560" height="315" src="https://www.youtube.com/embed/qnyD2OYF33c" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)        
                
    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 FODA 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-8
        p Luego de realizar el anlisis desde los dos enfoques definidos, se procede a realizar la matriz FODA que, dicho de otra manera, es la representacin grfica del diagnstico estratgico a partir del cual se disean las estrategias para poner en marcha la planeacin estratgica.
        P Realizar de forma adecuada el anlisis FODA ayuda a la organizacin y direccin a tener una visin amplia de los factores que los impactan de manera interna y externa, as mismo se pueden identificar las ventajas y desventajas del mercado en cualquier momento. Este anlisis se elabora en una matriz que busca identificar cada una de las variables, ubicarla en el cuadrante correcto y realizar un cruce de informacin para disear las estrategias que permitan aprovechar las oportunidades, robustecer las fortalezas, contrarrestar las amenazas y fortalecer los aspectos dbiles de la organizacin.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img38.svg', alt='La estrategia')
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p A continuacin, se muestra la estructura de la matriz FODA:

    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-md-8.desktop 
        .titulo-sexto.color-acento-contenido.offset-1
          h5 Figura 3.
          span  #[i  Estructura de la matriz FODA]
    
    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-md-8.desktop 
        figure
          img(src='@/assets/curso/temas/tema2/fig3.svg', alt='Anlisis interno:Fortalezas,Debilidades. Anlisis externo:Oportunidades: Amenazas')
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Al disear las estrategias se deben hacer cruces especficos para que estas tengan el resultado esperado, en ese orden de ideas, el cruce se realiza de la siguiente manera:
    
    .titulo-sexto.color-acento-contenido.offset-1(data-aos="fade-left")
      h5 Tabla 1.
      span  #[i  Matriz FODA]

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-9
        .tabla-a.color-acento-botones.mb-5
          table
            thead(style="background-color:#1B3F5E;color:#FFFFFF;")
              tr
                th 
                  h3(style="color:#FFFFFF;") MATRIZ FODA:
                  P(style="color:#FFFFFF;") (nombre de la organizacin)
                th Fortalezas (F)
                th Debilidades (D)
            tbody
              tr
                td Oportunidades (O)
                td Estrategia FO: estrategias para disear aprovechando las oportunidades del mercado y las fortalezas organizacionales.
                td Estrategia DO: estrategias para disear para fortalecer a la organizacin respecto a las oportunidades existentes.
              tr
                td Amenazas (A)
                td Estrategia FA: estrategias para disear para contrarrestar las amenazas con las fortalezas que tiene la organizacin.
                td Estrategia DO: estrategias para disear con ms cuidado en la planeacin pues es un cruce de las debilidades de la organizacin con las amenazas del exterior.
      .col-lg-3
        figure
          img(src='@/assets/curso/temas/tema2/img39.png', alt='Matriz DOFA')

    .text-decor.text-white.text-center(data-aos="flip-up").mb-5
      h3.mt-3.p-2 Objetivos
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12
        p Los objetivos de la organizacin ya se conocen, pues estn planteados desde la construccin de la misin, por lo que en este punto se hace referencia a los objetivos estratgicos que forman parte de la planeacin estratgica y que acompaan la formulacin de las estrategias para garantizar su cumplimiento. Tambin se debe considerar que las caractersticas de los objetivos sean medibles, alcanzables, claros, coherentes y que se puedan realizar en un lapso determinado.
        p En esta etapa de la planeacin los objetivos se definen con tres propsitos especficos y es hacia donde apuntan las organizaciones, estos son:

    .row.justify-content-center.mb-5(data-aos="fade-left")
      .col-12
        .row.mb-5
          .col-md-4.col-lg-4.mb-5.mb-lg-0
            .tarjeta-avatar
              img(src='@/assets/curso/temas/tema2/img40.svg' alt='AvatarTop').p-4
              //- .tarjeta debe ir acompaado de una de una de estas clases => 
              //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
              //- estas clases tambien tienen un modificador --borde
              .tarjeta.color-secundario
                .p-4
                  h2.text-center Materializar las estrategias
                  p.text-center Este propsito involucra a todo el equipo de trabajo, permite que se pongan de acuerdo y marca la ruta para lograr que la organizacin lo cumpla.
          .col-md-4.col-lg-4.mb-5.mb-lg-0
            .tarjeta-avatar
